
import XHeader from 'vux/src/components/x-header/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import mixin from '@/libs/vue/mixin'

export default {
name: 'Detail',
mixins: [mixin],
data () {
return {
list: [{
key: 'cx',
value: '储蓄'
}]
}
},
mounted () {
},
computed: {
ranking () {
return this.$store.getters.get_ranking
}
},
methods: {
kk () {
let hh = this.$store.getters.get_ranking
console.log(hh)
console.log('jj')
},

toggleMenu () {
this.$store.commit('dv_toggle_mode')
},

swipe () {
this.$emit('swipe', 1)
},

syn () {

if (window.applicationCache.status === window.applicationCache.UPDATEREADY) {
window.applicationCache.update()
}
window.location.reload()
},

setCurrent (code) {
this.$store.commit('tr_set_current', code)
},

up () {
let code = this.treasure.code.slice(0, -3)
this.$store.commit('tr_set_current', code)
},

viewInfo () {
let code = this.treasure.code
if (code !== '00') {
this.$router.push({
name: 'Info',
params: {
isCurrent: 1
}
})
}
},

view (item) {
console.log(this.prompt)
this.$router.push({
path: '/bill',
query: {
...item
}
})
},

add () {
let list = this.$store.getters.get_wealth
let len = list.length
if (len) {
this.$router.push({
path: '/bill'
})
} else {
this.$prompt('当前集合没有财富，请创建')
}
}
},
components: {
XHeader, Scroller
}
}



